import React, { useState, useEffect } from 'react';
import MenuBarItem from './MenuBarItem';
import { Link } from 'react-router-dom';
import logo from '../../static/images/6to1_wood.png';
import './menubar.css';


const MenuBar = (props) => {
    const [menubar, setMenubar] = useState("navbar-fixed");
    const [subMenu, setSubMenu] = useState(false);

    useEffect(() => {
        if (window.scrollY > 1) setMenubar("navbar");
    })

    useEffect(() => {
        document.addEventListener('scroll', function () {
            if (window.scrollY > 5) {
                setMenubar("navbar")
            }

            if (window.innerWidth < 770) {
                setMenubar("navbar")
            }

            if (window.scrollY < 3) {
                setMenubar("navbar-fixed")
            }
        })
    })

    const handleMenuClick = () => {
        if (subMenu) {
            setSubMenu(false)
            if (menubar === "navbar-fixed") {
                document.getElementById("root").style.marginLeft = "0px";
                document.getElementById("navbar-fixed").style.marginLeft = "0px";
                document.body.style.backgroundColor = "white";

            }

            else {
                document.getElementById("root").style.marginLeft = "0px";
                document.getElementById("navbar").style.marginLeft = "0px";
                document.body.style.backgroundColor = "white";
            }
        }

        else {
            setSubMenu(true)
            if (menubar === "navbar-fixed") {
                document.getElementById("root").style.marginLeft = "250px";
                document.getElementById("navbar-fixed").style.marginLeft = "250px";

            }

            else {
                document.getElementById("root").style.marginLeft = "250px";
                document.getElementById("navbar").style.marginLeft = "250px";
            }
        }
    }

    const menuBarItems = [
        { 'key': 1, 'to': '/', 'itemValue': 'Home' },
        { 'key': 2, 'to': '/projects', 'itemValue': 'Projects' },
        { 'key': 3, 'to': '/about', 'itemValue': 'About' },
        { 'key': 4, 'to': '/contact', 'itemValue': 'Contact' },
    ]

    return (
        <div id={menubar} className="navbar fixed-top">
            <div id="hamburgerMenu">
                <div className={subMenu ? "change" + " menu-container" : "menu-container"} onClick={handleMenuClick}>
                    <div className="bar1"></div>
                    <div className="bar2"></div>
                    <div className="bar3"></div>
                </div>
                {subMenu ? <div className="dropdown-menu" style={{ display: 'block' }} aria-labelledby="dropdownMenuLink">
                    {menuBarItems.map(item => {
                        return (
                            <Link onClick={() => document.getElementById("root").style.marginLeft = "0px"} key={item.key} className="dropdown-item" to={item.to}>{item.itemValue}</Link>
                        )
                    })}
                </div> : <div></div>}
            </div>
            <div style={{ display: 'flex', width: '100%' }}>
                <div>
                    <img className="logoScroll" src={logo} alt="Cannot be displayed" />
                </div>
                <div className="menuItemContainerScroll">
                    {menuBarItems.map(item => {
                        return (
                            <MenuBarItem
                                key={item.key}
                                to={item.to}
                                itemValue={item.itemValue}
                            />
                        )
                    })}
                </div>
            </div>
        </div>
    )
}

export default MenuBar;